
import { getCurrentInstance, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { login } from "../../api/index.js";



export default {
  setup() {
    const router = useRouter();
    const { ctx }: any = getCurrentInstance();
    const ruleFormsss = ref<any>(null);
    // 定义变量
    const ruleForm = reactive({
      phone: "17391963670",
      password: "syt981125",
    });
    // 定义验证规则
    const rules = {
      phone: [
        { required: true, message: "请输入手机号", trigger: "blur" },
        { min: 11, max: 11, message: "请输入正确的手机号", trigger: "blur" },
      ],
      password: [
        { required: true, message: "请输入密码", trigger: "blur" },
        { min: 6, max: 20, message: "密码长度不能小于6位", trigger: "blur" },
      ],
    };
    // 确认
    const submitForm = () => {
      try {
        (ruleFormsss.value as any).validate((valid: boolean) => {
          if (valid) {
            console.log(ruleForm);
            const { phone, password } = ruleForm;
            
            login({phone:phone,password:password}).then((res:any) => {
              if(res.err === false){
                // ctx.$message.success('登录成功了~');
                alert('登录成功了~');
                console.log("login.response",res);
                router.push('/home/manageMenu');
                
              }
            });
          }
        });
      } catch (error) {
        console.log(error);
      }
    };
    return {
      ruleForm,
      rules,
      submitForm,
      ruleFormsss,
    };
  },
};
